



































































































import { Component, Mixins } from 'vue-property-decorator'
import getRepository from '@/services'
import EmployeeRepository from '@/services/repositories/employee'
import { namespace } from 'vuex-class'
import ImportFilter from '../filter/FilterPa.vue'
import PopupMixin from '@/mixins/popup'
const beUser = namespace('beUser')
const beEmployee = namespace('beEmployee')

@Component({ components: { ImportFilter } })
export default class EmployeeUnitInfo extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any
  @beEmployee.State('unitDetail') unitDetail!: Record<string, any>
  // @Prop({ required: false, type: Array, default: [] }) memberInfor!: Record<string, any>
  // @Prop({ default: [] }) dataSelect?: Array<Record<string, any>>
  api!: EmployeeRepository
  pageQuery = {
    // locationId: this.memberInfor?.id,
    limit: 10,
    page: 1,
    total: 0,
    search: '' // ps: 10,
    // i: 0,
  }
  searchValue = ''
  paginitInfo = {
    i: 0,
    ps: 20,
    limit: 10,
    page: 1,
    search: '',
    sort: 1,
    locationId: 0,
    statusAccount: 0,
    // groupTitle: '',
    total: 0
  }
  dataList: any = []
  handleSearch(): void {
    // this.loading = true
    this.pageQuery.page = 1
    setTimeout(() => {
      this.pageQuery.search = this.searchValue
      this.init()
    }, 500)
    // this.paginitInfo.search = this.searchValue
  }
  // handleSearch(): void {
  //   this.loading = true
  //   this.paginitInfo.page = 1
  //   setTimeout(() => {
  //     this.paginitInfo.search = this.searchValue
  //     this.getDataMembers()
  //     this.getTotalMembers()
  //   }, 500)
  //   // this.paginitInfo.search = this.searchValue
  // }
  handleOpen(): void {
    this.init()
  }
  created(): void {
    this.api = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
  }
  handleSizeChange(value: number): void {
    this.pageQuery.page = 1
    this.pageQuery.limit = value
    this.init()
  }
  handleCurrentChange(value: number): void {
    this.pageQuery.page = value
    this.init()
  }

  init(): void {
    const temp = {
      ...this.pageQuery,
      locationId: this.unitDetail?.id
    }
    this.api
      .all(temp)
      .then(data => {
        this.dataList = data
      })
      .catch(e => {
        console.log(e)
      })
    this.api
      .getCountMembers(temp)
      .then(data => {
        this.pageQuery.total = +data
      })
      .catch(error => {
        console.log(error)
      })
  }
  handleClose(): void {
    console.log('close')
  }
}
