

























































import { Component, Prop, Mixins } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
//   import UnitItemChild from ''
import PopupMixin from '@/mixins/popup'
const UnitItemChild = () => import('../unit/UnitItem1.vue')

import getRepository from '@/services'
import LocationRepository from '@/services/repositories/location'
const beUser = namespace('beUser')
const beEmployee = namespace('beEmployee')
@Component({ components: { UnitItemChild } })
export default class UnitItem extends Mixins(PopupMixin) {
  @Prop({ required: true, type: Object, default: {} }) value!: Record<string, any>
  @Prop({ required: false, type: Array, default: [] }) parent!: []
  @Prop({ required: false, type: String, default: '' }) checkValue!: string
  @Prop({ required: false, type: Boolean, default: false }) isSearch!: boolean

  @beUser.State('user') user!: any
  @beUser.State('role') roleMembers!: any
  @beEmployee.Action('setUnitDetail') setUnitDetail!: (payload: Record<string, any>) => void

  isOpen = false
  childParent: Array<Record<string, any>> = []
  memberInfo: Record<string, unknown> = {}
  api!: LocationRepository

  created(): void {
    this.api = getRepository('location', {
      districtId: this.user?.districts?.[0].id
    })
  }
  check = false
  checkRole(listRole: Record<string, any>): void {
    // listRole.includes(this.roleMembers === 'ADMIN') ? false : true
    if (listRole.includes('ADMIN')) {
      this.check = true
    } else {
      this.check = false
    }
    console.log('check', this.check)
  }
  mounted(): void {
    this.childParent = [...this.parent, ...[this.value.id]]
    this.checkRole(this.roleMembers.userRole)
    //   this.getLocationList()
    // this.getTotalMembers()
  }
  get isOpenChild(): boolean {
    return this.isOpen
  }

  handleInfo(): void {
    this.setUnitDetail(this.value)
    this.setOpenPopup({
      popupName: 'employeeunit',
      isOpen: true
    })
  }
  dataPopup = {}
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  editItem(data: Record<string, any>) {
    this.$emit('edit', data)
  }
  createItem(type: string) {
    this.$emit('createLo', 'location')
  }
  handleEditUnit(dataPop: Record<string, any>): void {
    this.$emit('edit', dataPop)
    this.setOpenPopup({
      popupName: 'edit-unit',
      isOpen: true
    })
  }
  handleAddBranch(value: Record<string, any>, type: string): void {
    this.$emit('idLocation', value.id)
    this.$emit('create', type, value.id)
    console.log('1', type)
    this.setOpenPopup({
      popupName: 'create-unit',
      isOpen: true
    })
  }
  handleAddLocation(type: string): void {
    this.$emit('createLo', type)
    this.setOpenPopup({
      popupName: 'create-unit',
      isOpen: true
    })
  }

  handleOpenClick(value: Record<string, any>): void {
    const temp = {
      parentId: value.id
    }
    if (!this.isOpen && (value.numOfChildren || value.childrens.length === 0)) {
      this.api.getListLocationTree(temp).then((res: any) => {
        value.children = res
        this.isOpen = !this.isOpen
        this.$forceUpdate()
      })
    } else {
      this.isOpen = !this.isOpen
    }
  }
}
