




















































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import UserRepository from '@/services/repositories/user'
import EmployeeRepository from '@/services/repositories/employee'
import TransactionRepository from '@/services/repositories/transaction'
import LocationRepository from '@/services/repositories/location'
import { trim, debounce } from 'lodash'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class EditUnit extends Mixins(PopupMixin) {
  @Prop() dataProp: any
  @beUser.State('user') user?: Record<string, any>
  api!: TransactionRepository
  apiUser!: UserRepository
  apiEmployee!: EmployeeRepository
  apiLocation!: LocationRepository
  locationRes: LocationRepository | undefined = undefined
  branchList: any = []
  saleMembersList: any = []
  areaList = []
  isUpload: any = false
  imageUrl: any = ''
  form: any = {}
  childrenList = []
  checkBranchList = []
  dataInput: any = {
    locationCode: '',
    name: '',
    parentId: '',
    leaderId: '',
    leaderMemberCode: '',
    leaderFullName: '',
    status: true,
    locationType: '',
    contactEmailList: '',
    transactionOfficeType: ''
  }
  paymentPeriodList = [
    {
      value: 'HO',
      lable: 'HO'
    },
    {
      value: 'CN',
      lable: 'CN'
    },
    {
      value: 'TSC',
      lable: 'TSC'
    },
    {
      value: 'PGD',
      lable: 'PGD'
    },
    {
      value: 'PGDBD',
      lable: 'PGDBD'
    }
  ]
  dataForm: any = {
    locationCode: '',
    name: '',
    parentId: '',
    leaderId: '',
    leaderMemberCode: '',
    leaderFullName: '',
    status: true,
    checkMessage: false,
    checkChildern: false
  }
  leaderData: any = {
    leaderId: '',
    leaderMemberCode: '',
    leaderFullName: ''
  }
  rules: any = {
    locationCode: [
      {
        required: true,
        message: 'Mã ĐVKD không được để trống',
        trigger: 'blur'
      }
    ],
    name: [
      {
        required: true,
        message: 'Tên ĐVKD không được để trống',
        trigger: 'blur'
      }
    ],
    leaderId: [
      {
        required: true,
        message: 'Lãnh đạo quản lý không được để trống',
        trigger: 'blur'
      }
    ],
    parentId: [
      {
        required: true,
        message: 'Chi nhánh không được để trống',
        trigger: 'blur'
      }
    ],
    transactionOfficeType: [
      {
        required: true,
        message: 'Loại ĐVKD không được để trống',
        trigger: 'blur'
      }
    ]
  }
  memberData: any = {}
  handleChangeInput(value: string): void {
    let memberList = this.listRelationship.filter((item: any) => item.id == value)
    this.memberData = memberList[0]
    console.log('memeber', this.memberData)
    this.leaderData.leaderId = this.memberData.id
    this.leaderData.leaderFullName = this.memberData.fullName
    this.leaderData.leaderMemberCode = this.memberData.memberNo
  }
  showAvatar(file: any): void {
    this.imageUrl = file.raw
    this.isUpload = true
    const isLt2M = file.size / 1024 / 1024 < 10
    if (!isLt2M) {
      this.$message.error('Ảnh không được quá 20mb')
    } else {
      this.form.avatar = URL.createObjectURL(file.raw)
      this.$forceUpdate()
    }
  }
  handleAvatarSuccess(file: any): void {
    this.imageUrl = URL.createObjectURL(file.raw)
  }
  beforeAvatarUpload(file: any): boolean {
    const isJPG = file.type === 'image/jpeg'
    const isLt2M = file.size / 1024 / 1024 < 2

    if (!isJPG) {
      this.$message.error('Avatar picture must be JPG format!')
    }
    if (!isLt2M) {
      this.$message.error('Avatar picture size can not exceed 2MB!')
    }
    return isJPG && isLt2M
  }
  handleChangeStatus(check: boolean): void {
    if (check == false) {
      if (this.dataProp.levelDepth == 1) {
        if (this.dataForm.checkMessage == true) {
          this.$message({
            message: 'Cập nhật thất bại do còn ĐVKD trực thuộc đang hoạt động',
            type: 'error'
          })
          this.dataForm.status = true
          // this.dataInput.status = 'ACTIVE'
        } else {
          this.dataForm.status = false
          // this.dataInput.status = 'INACTIVE'
        }
      }
    }
    if (check == true) {
      if (this.dataProp.levelDepth == 2) {
        if (this.dataForm.checkChildern == false) {
          this.$message({
            message: 'Cập nhật thất bại do ĐVQL không hoạt động. Vui lòng cập nhật đúng ĐVQL hoạt động & thử lại.',
            type: 'error'
          })
          this.dataForm.status = false
          // this.dataInput.status = 'INACTIVE'
        } else {
          this.dataForm.status = true
          // this.dataInput.status = 'ACTIVE'
        }
      }
    }
    if (this.dataForm.status == true) {
      this.dataInput.status = 'ACTIVE'
    } else {
      this.dataInput.status = 'INACTIVE'
    }
  }
  //api nhan vien phụ trách
  listRelationship: Array<Record<string, any>> = []
  querySaleMember = {
    i: 0,
    ps: 20,
    search: ''
  }
  loadMoreSaleMember(): void {
    this.querySaleMember.i += 20
    const a = debounce(this.getSaleMemberList, 500)
    a(this.querySaleMember.search, true)
  }

  remoteSaleMembers(query: string): void {
    this.querySaleMember.search = trim(query)
    this.querySaleMember.i = 0
    this.querySaleMember.ps = 20
    const a = debounce(this.getSaleMemberList, 500)
    a(query)
  }

  async getSaleMemberList(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.apiEmployee.getEmployeeList({
        search: trim(search),
        i: this.querySaleMember.i,
        ps: this.querySaleMember.ps,
        statusAccount: 0,
        groupCode: 'INSURANCE'
      })
      if (isAdd) {
        const a = result.data || []
        this.listRelationship = [...this.listRelationship, ...a]
      } else {
        this.listRelationship = result.data || []
      }
      const params = {
        id: +this.dataProp.leaderId,
        memberNo: this.dataProp.leaderMemberCode,
        fullName: this.dataProp.leaderFullName
      }
      this.listRelationship.push(params)
    } catch (error) {
      this.listRelationship = []
    }
  }

  async getLocationList(query: string): Promise<any> {
    await this.apiUser
      ?.getAreaList({
        search: trim(query),
        type: 1,
        i: 0,
        districtId: this.user?.districts?.[0].id,
        ps: 20
      })
      .then(list => {
        this.areaList = list.data
      })
      .catch(error => console.log(error))
  }
  query = {
    branches: {
      i: 0,
      ps: 10000,
      search: '',
      type: 2
    }
  }
  loadMoreBranch(): void {
    this.query.branches.i += 20
    const a = debounce(this.getBranch, 100)
    a(this.query.branches.search, true)
  }

  remoteBranch(query: string): void {
    this.query.branches.search = trim(query)
    this.query.branches.i = 0
    this.query.branches.ps = 20
    const a = debounce(this.getBranch, 500)
    a(query)
  }

  async getBranch(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.locationRes?.list('branch', {
        search: trim(search),
        i: this.query.branches.i,
        ps: this.query.branches.ps
      })
      if (isAdd) {
        const a = result || []
        this.branchList = [...this.branchList, ...a]
      } else {
        this.branchList = result || []
      }
    } catch (error) {
      this.branchList = []
    }
    console.log('branch', this.branchList)
  }
  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'edit-unit',
      isOpen: false
    })
  }
  async updateAvatar(locationId: number | string): Promise<void> {
    const formData = new FormData()
    formData.append('avatar', this.imageUrl)
    if (this.imageUrl) {
      await this.api
        ?.uploadAvatar(locationId, formData)
        .then(res => {
          this.form.avatar = res
          // this.$message.success('Lưu thành công')
        })
        .catch(() => {
          // this.form.avatar = this.dataInput.avatar
          // this.$message.warning('Lưu thất bại')
        })
    }
    this.isUpload = false
  }
  handleSave(): void {
    let a: any = this.$refs.dataInput
    a.validate((valid: any) => {
      if (valid) {
        if (this.dataProp.levelDepth == 1) {
          this.dataInput.locationType = 'BRANCH'
        } else {
          this.dataInput.locationType = 'TRANSACTION_OFFICE'
        }
        this.handleChangeInput(this.dataInput.leaderId)
        const params = {
          ...this.leaderData,
          name: this.dataInput.name ? this.dataInput.name : null,
          locationCode: this.dataInput.locationCode ? this.dataInput.locationCode : null,
          parentId: this.dataInput.parentId ? this.dataInput.parentId : null,
          status: this.dataInput.status ? this.dataInput.status : null,
          locationType: this.dataInput.locationType ? this.dataInput.locationType : null,
          contactEmailList: this.dataInput.contactEmailList ? this.dataInput.contactEmailList : null,
          transactionOfficeType: this.dataInput.transactionOfficeType ? this.dataInput.transactionOfficeType : null
        }

        const locationId = this.dataInput.id

        this.api
          ?.updateLocation(params, locationId)
          .then(() => {
            this.updateAvatar(locationId)
            this.$message.success('Cập nhật đơn vị thành công')
            this.setOpenPopup({
              popupName: 'edit-unit',
              isOpen: false
            })
            this.$emit('edit', 123)
          })
          .catch(er => {
            this.$message.error(er.message)
          })
      } else {
        console.log('error validate data!')
        return false
      }
    })
  }
  created(): void {
    this.locationRes = getRepository('location', {
      districtId: this.districtId
    })
    this.apiUser = getRepository('user', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiEmployee = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.api = getRepository('transaction', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiLocation = getRepository('location', {
      districtId: this.user?.districts?.[0].id
    })
  }
  response: any = {}
  getDetailLocation(id: number | string): void {
    this.api
      .getDetailLocation(id)
      .then((response: any) => {
        this.response = response
        this.dataInput = response
        this.form.avatar = response.avatar
        if (this.dataInput.leaderId == 0) {
          this.dataInput.leaderId = null
        }
        // this.dataInput.dataInput.status = true
        if (this.dataInput.status == 'ACTIVE') {
          this.dataForm.status = true
        } else {
          this.dataForm.status = false
        }
        if (this.dataInput.parentId == 0) {
          this.dataInput.parentId = ''
          console.log('id', this.dataInput.parentId)
        }

        this.getChildenList()
      })
      .catch((err: any) => console.log(err))
  }
  childrenBranch: any = {}
  getChildenList(): void {
    if (this.dataProp.levelDepth == 1) {
      const temp = {
        parentId: this.dataProp.id
      }
      this.apiLocation.getListLocationTree(temp).then((res: any) => {
        this.childrenList = res
        let checkList = this.childrenList.filter((item: any) => item.status == 'ACTIVE')
        if (checkList.length > 0) {
          this.dataForm.checkMessage = true
        } else {
          this.dataForm.checkMessage = false
        }
      })
    } else {
      this.checkBranchList = this.branchList.filter((item: any) => item.id == this.dataProp.parentId)
      this.childrenBranch = this.checkBranchList[0]

      if (this.childrenBranch.status == 'ACTIVE') {
        this.dataForm.checkChildern = true
      } else {
        this.dataForm.checkChildern = false
      }
    }
  }
  handleOpen(): void {
    if (this.dataProp.levelDepth == 1) {
      this.getLocationList('')
    } else {
      this.getBranch('')
    }
    this.getSaleMemberList('')
    this.getDetailLocation(this.dataProp.id)
    console.log('cai nay', this.dataProp)
  }
  handleClose(): void {
    // @ts-ignore
    this.$refs.dataInput.clearValidate()
    this.setOpenPopup({
      popupName: 'edit-unit',
      isOpen: false
    })
    this.dataInput = {}
    this.form = {}
  }
  // form = {
  //   delivery: true
  // }
}
