



































































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import UserRepository from '@/services/repositories/user'
import EmployeeRepository from '@/services/repositories/employee'
import TransactionRepository from '@/services/repositories/transaction'
import { trim, debounce } from 'lodash'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class CreateUnit extends Mixins(PopupMixin) {
  @beUser.State('user') user?: Record<string, any>
  @Prop() checkType: any
  // @Prop() dataProp: any
  @Prop() getIdFirst: any
  api!: TransactionRepository
  apiUser!: UserRepository
  apiEmployee!: EmployeeRepository
  isUpload: any = false
  imageUrl: any = ''
  showPicutreInitial = true
  branchList: any = []
  saleMembersList: any = []
  form: any = {}
  areaList: any = {}
  paymentPeriodList = [
    {
      value: 'HO',
      lable: 'HO'
    },
    {
      value: 'CN',
      lable: 'CN'
    },
    {
      value: 'TSC',
      lable: 'TSC'
    },
    {
      value: 'PGD',
      lable: 'PGD'
    },
    {
      value: 'PGDBD',
      lable: 'PGDBD'
    }
  ]

  dataInput: any = {
    code: '',
    name: '',
    parentId: '',
    leaderId: '',
    leaderMemberCode: '',
    leaderFullName: '',
    contactEmailList: '',
    transactionOfficeType: ''
  }
  rules: any = {
    code: [
      {
        required: true,
        message: 'Mã ĐVKD không được để trống',
        trigger: 'blur'
      }
    ],
    name: [
      {
        required: true,
        message: 'Tên ĐVKD không được để trống',
        trigger: 'blur'
      }
    ],
    salesMemberId: [
      {
        required: true,
        message: 'Lãnh đạo quản lý không được để trống',
        trigger: 'blur'
      }
    ],
    parentId: [
      {
        required: true,
        message: 'Chi nhánh không được để trống',
        trigger: 'blur'
      }
    ],
    transactionOfficeType: [
      {
        required: true,
        message: 'Loại ĐVKD không được để trống',
        trigger: 'blur'
      }
    ]
  }
  showAvatar(file: any): void {
    this.imageUrl = file.raw
    this.isUpload = true
    const isLt2M = file.size / 1024 / 1024 < 10
    if (!isLt2M) {
      this.$message.error('Ảnh không được quá 20mb')
    } else {
      this.form.avatar = URL.createObjectURL(file.raw)
      console.log('avatar2', this.form.avatar)

      this.$forceUpdate()
    }
  }
  handleAvatarSuccess(file: any): void {
    this.imageUrl = URL.createObjectURL(file.raw)
  }
  beforeAvatarUpload(file: any): boolean {
    const isJPG = file.type === 'image/jpeg'
    const isLt2M = file.size / 1024 / 1024 < 2

    if (!isJPG) {
      this.$message.error('Avatar picture must be JPG format!')
    }
    if (!isLt2M) {
      this.$message.error('Avatar picture size can not exceed 2MB!')
    }
    return isJPG && isLt2M
  }
  async getBranchList(query: string): Promise<any> {
    await this.apiUser
      ?.getAreaList({
        search: trim(query),
        type: 2,
        i: 0,
        districtId: this.user?.districts?.[0].id,
        ps: 20
      })
      .then(list => {
        this.branchList = list.data
      })
      .catch(error => console.log(error))
  }
  listValue = []
  handleChangeInput(value: string): void {
    // console.log('1', value1)
    const listValue = value.split(',')
    ;(this.dataInput.leaderId = listValue[0]), (this.dataInput.leaderMemberCode = listValue[1]), (this.dataInput.leaderFullName = listValue[2])
  }
  async updateAvatar(locationId: number | string): Promise<void> {
    const formData = new FormData()
    formData.append('avatar', this.imageUrl)
    if (this.imageUrl) {
      await this.api
        ?.uploadAvatar(locationId, formData)
        .then(res => {
          this.form.avatar = res
          // this.$message.success('Lưu thành công')
        })
        .catch(() => {
          // this.form.avatar = this.dataInput.avatar
          // this.$message.warning('Lưu thất bại')
        })
    }
    this.isUpload = false
  }
  handleSave(): void {
    let a: any = this.$refs.dataInput
    a.validate((valid: any) => {
      if (valid) {
        if (this.checkType == 'location') {
          this.dataInput.locationType = 'TRANSACTION_OFFICE'
        } else {
          this.dataInput.locationType = 'BRANCH'
          this.dataInput.transactionOfficeType = 'CN'
        }
        if (this.dataInput.transactionOfficeType == 'CN') {
          this.dataInput.locationType = 'BRANCH'
        }
        const params = {
          ...this.dataInput,
          salesMemberId: null,
          name: this.dataInput.name,
          locationCode: this.dataInput.code,
          parentId: this.dataInput.parentId,
          transactionOfficeType: this.dataInput.transactionOfficeType,
          // locationType: 'TRANSACTION_OFFICE',
          status: 'ACTIVE',
          contactEmailList: this.dataInput.contactEmailList
        }
        delete params.salesMemberId
        delete params.code
        this.api
          ?.createLocation(params)
          .then((res: Record<string, any>) => {
            this.updateAvatar(res.id)
            this.$message.success('Thêm đơn vị thành công')
            this.handleClose()
            this.$emit('save')
          })
          .catch(er => {
            this.$message.error(er.message)
          })
      } else {
        console.log('error validate data!')
        return false
      }
    })
  }
  remoteBranch(query: string): void {
    if (query == '' || query == null) {
      this.apiUser
        ?.getAreaList({
          search: '',
          type: 2,
          i: 0,
          districtId: this.user?.districts?.[0].id,
          ps: 20
        })
        .then(list => {
          this.branchList = list.data
        })
        .catch(error => console.log(error))
    } else {
      this.apiUser
        ?.getAreaList({
          search: query,
          type: 2,
          i: 0,
          districtId: this.user?.districts?.[0].id,
          ps: 20
        })
        .then(list => {
          this.branchList = list.data
        })
        .catch(error => console.log(error))
    }
  }

  //api nhan vien phụ trách
  listRelationship: Array<Record<string, any>> = []
  querySaleMember = {
    i: 0,
    ps: 20,
    search: '',
    statusAccount: 0,
    groupCode: 'INSURANCE',
    sort: 1
  }
  loadMoreSaleMember(): void {
    this.querySaleMember.i += 20
    const a = debounce(this.getSaleMember, 500)
    a(this.querySaleMember.search, true)
  }

  remoteSaleMembers(query: string): void {
    this.querySaleMember.search = trim(query)
    this.querySaleMember.i = 0
    this.querySaleMember.ps = 20
    const a = debounce(this.getSaleMember, 500)
    a(query)
  }

  async getSaleMember(search: string, isAdd = false): Promise<any> {
    try {
      const result = await this.apiEmployee.getEmployeeList({
        search: trim(search),
        i: this.querySaleMember.i,
        ps: this.querySaleMember.ps
      })
      if (isAdd) {
        const a = result.data || []
        this.listRelationship = [...this.listRelationship, ...a]
      } else {
        this.listRelationship = result.data || []
      }
    } catch (error) {
      this.listRelationship = []
    }
  }

  handleClose(): void {
    // @ts-ignore
    this.$refs.dataInput.clearValidate()
    this.imageUrl = ''
    this.setOpenPopup({
      popupName: 'create-unit',
      isOpen: false
    })
    this.dataInput = {}
    this.form = {}
  }

  async getLocationList(query: string): Promise<any> {
    await this.apiUser
      ?.getAreaList({
        search: trim(query),
        type: 1,
        i: 0,
        districtId: this.user?.districts?.[0].id,
        ps: 20
      })
      .then(list => {
        this.areaList = list.data
      })
      .catch(error => console.log(error))
  }
  handleOpen(): void {
    this.getLocationList('')
    if (this.checkType == 'location') {
      this.getBranchList('')
    } else {
      this.dataInput.parentId = this.getIdFirst
    }
    this.getSaleMember('')
  }
  handleReset(): void {
    // @ts-ignore
    this.$refs.dataInput.clearValidate()
    this.dataInput = {}
    ;(this.dataInput.code = null), (this.dataInput.name = null), (this.dataInput.salesMemberId = null), (this.dataInput.branchId = null)
  }
  created(): void {
    this.apiUser = getRepository('user', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiEmployee = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.api = getRepository('transaction', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
