


























































import PopupCreateUnit from '../components/popup/PopupCreateUnit.vue'
import PopupEditUnit from '../components/popup/PopupEditUnit.vue'
import { Component } from 'vue-property-decorator'
import getRepository from '@/services'
import LocationRepository from '@/services/repositories/location'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
//@ts-ignore
import UnitItem from '../components/unit/UnitItem1.vue'
import EmployeeUnitInfo from '../components/popup/EmployeeUnitInfo.vue'

const beUser = namespace('beUser')
@Component({ components: { UnitItem, EmployeeUnitInfo, PopupCreateUnit, PopupEditUnit } })
export default class EmployeeUnit extends PopupMixin {
  @beUser.State('user') user!: any
  @beUser.State('role') roleMembers!: any
  api!: LocationRepository
  respones = []
  locationList = []
  dataList = []
  listTotal = []
  data = []
  valueActive = ''
  dropDownText = 'Hoạt động'
  isSearch = false
  locationId = 0
  locationRes: LocationRepository | undefined = undefined
  pageQuery = {
    page: 1,
    limit: 20,
    total: 0,
    // ps: 10,
    search: '',
    // i: 0,
    status: 'ACTIVE'
  }
  options = [
    {
      value: '1',
      label: 'Hoạt động'
    },
    {
      value: '2',
      label: 'Không hoạt động'
    }
  ]

  statusList = [
    {
      id: 0,
      label: 'Hoạt động',
      value: 'ACTIVE'
    },
    {
      id: 1,
      label: 'Không hoạt động',
      value: 'INACTIVE'
    }
  ]
  paymentPeriodList = [
    {
      value: '1',
      lable: '1'
    },
    {
      value: '2',
      lable: '2'
    },
    {
      value: '4',
      lable: '4'
    }
  ]
  idArea = ''
  searchValue = ''
  status = 'Hoạt động'

  childrens = []
  check = false
  checkRole(listRole: Record<string, any>): void {
    // listRole.includes(this.roleMembers === 'ADMIN') ? false : true
    if (listRole.includes('ADMIN')) {
      this.check = true
    } else {
      this.check = false
    }
    console.log('check', this.check)
  }
  mounted(): void {
    const temp = {
      parentId: 0
    }
    this.api.getListLocationTree(temp).then((res: any) => {
      this.data = res
    })
    this.checkRole(this.roleMembers.userRole)
  }
  params = {
    page: 1,
    limit: 10,
    ps: 10,
    i: 10
  }
  dataPopup = {}
  type = ''
  handleCommand(command: string): void {
    if (command == 'ACTIVE') {
      this.dropDownText = 'Hoạt động'
    } else {
      this.dropDownText = 'Không hoạt động'
    }
    this.pageQuery.status = command
    if (command == 'ACTIVE') {
      this.isSearch = false
      if (this.searchValue == '' || this.searchValue == null) {
        this.isSearch = false
        setTimeout(() => {
          this.getFirstList()
        }, 500)
      } else {
        this.isSearch = true
        setTimeout(() => {
          this.getLocationList()
        }, 500)
      }
    } else {
      this.isSearch = true
      setTimeout(() => {
        this.getLocationList()
      }, 100)
    }
  }
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  editItem(data: Record<string, any>) {
    this.dataPopup = data
  }
  createItem(data: string): void {
    this.type = data
  }
  getIdArea(id: string): void {
    this.idArea = id
  }
  createBranch(type: string): void {
    this.type = type
  }

  expandRowKeys: any = []
  handleExpandChange(data: { row: any; expandedRows: any }): void {
    const id = data.row.id
    const temp = {
      parentId: id
    }
    this.api.getListLocationTree(temp).then((res: any) => {
      this.dataList = res
    })
  }
  hackleClickAddUnit(type: string): void {
    this.type = type
    this.setOpenPopup({
      popupName: 'create-unit',
      isOpen: true
    })
  }

  handleSearch(): void {
    this.pageQuery.page = 1
    this.pageQuery.search = this.searchValue
    if (this.searchValue == '' || this.searchValue == null) {
      this.isSearch = false
      setTimeout(() => {
        this.getFirstList()
      }, 500)
    } else {
      this.isSearch = true
      setTimeout(() => {
        this.getLocationList()
      }, 500)
    }
  }
  handleSizeChange(value: number): void {
    this.params.page = 1
    this.params.limit = value
    this.pageQuery.page = 1
    this.pageQuery.limit = value
    this.getLocationList()
  }
  handleCurrentChange(value: number): void {
    this.pageQuery.page = value
    this.getLocationList()
  }
  handleChangeStatsus(value: string): void {
    this.pageQuery.status = value
    if (value == 'ACTIVE') {
      this.isSearch = false
      if (this.searchValue == '' || this.searchValue == null) {
        this.isSearch = false
        setTimeout(() => {
          this.getFirstList()
        }, 500)
      } else {
        this.isSearch = true
        setTimeout(() => {
          this.getLocationList()
        }, 500)
      }
    } else {
      this.isSearch = true
      setTimeout(() => {
        this.getLocationList()
      }, 100)
    }
  }
  getLocationList(): void {
    // const total = {
    //   search: this.searchValue
    // }
    this.api.getLocationList(this.pageQuery).then((res: any) => {
      this.data = res
    })
  }
  getFirstList(): void {
    const temp = {
      parentId: 0
    }
    this.api.getListLocationTree(temp).then((res: any) => {
      this.data = res
    })
  }
  handleFilter(value: Record<string, any>): void {
    this.pageQuery.page = 1
    this.getFirstList()
  }
  created(): void {
    this.api = getRepository('location', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
